import Dep, {pushTarget, popTarget} from './dep'
import { parsePath } from './parsePath';
let uid = 0
 
export default function Watcher(vm, expOrFn,cb, options) {
  this.id = ++uid // uid for batching
  this.expOrFn = expOrFn;
  this.vm = vm;
  this.deps = []
  this.depIds = new Set();
  this.cb = cb;
  if (options) {
    this.user = !!options.user;// new vue({watch:{}})
    this.lazy = !!options.lazy;
    
  } else {
    this.user = this.lazy = false
  }
 
  this.dirty = this.lazy // watcherDep.target
  // getterdepObserver
  this.getter = typeof expOrFn === 'function' ? this.expOrFn : parsePath(expOrFn);
  this.value = this.lazy ? undefined :this.get();
  
}
 
Watcher.prototype.get = function() {
  let value;
  pushTarget(this)
  
  // if (this.dirty) Dep.target = this
  // Dep.target = computedWatcher
  value = this.getter.call(this.vm,this.vm);
  // if (this.dirty) Dep.target = null
  popTarget();
  return value
}
 
Watcher.prototype.update = function() {
  //computed watcher
  if (this.lazy) {
    this.dirty = true;
  } else {
    this.run();
  }
}
 
Watcher.prototype.addDep = function(dep) {
  const id = dep.id
  if (!this.depIds.has(id)) {
    //this.deps.push (this.num dep)
    this.deps.push(dep)
    this.depIds.add(id)
    dep.addSub(this);
    //this.num dep.sub.push +  computedWatcher
  }
}
 
Watcher.prototype.evaluate = function() {
  this.value = this.get()
  this.dirty = false
}
 
Watcher.prototype.depend = function() {
  let i = this.deps.length
  while (i--) {
    this.deps[i].depend()
  }
}
Watcher.prototype.run = function(){
  const value = this.get();
  if(value !== this.value){
    const oldValue = this.value
    this.value = value
    this.cb.call(this.vm,value,oldValue);
    console.log(this.cb);
  }
}
